import React, { FC } from 'react';
import {
  boolColumn,
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, CodigoDctf } from '../../../../models/dtos/mlf/codigoDctf';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const CodigoDctfSearchView: FC<SearchViewProps<CodigoDctf>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    boolColumn({
      dataField: 'pis',
      editable: false,
      text: 'PIS',
    }),
    boolColumn({
      dataField: 'cofins',
      editable: false,
      text: 'COFINS',
    }),
  ];

  return (
    <WindowComponent title="Códigos DCTF para PIS/COFINS">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'CodigoDctfEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default CodigoDctfSearchView;
